import React, { useState } from "react";
import "./App.css";
import Items from "./components/Items";
import Cart from "./components/Cart";

const page_items = "items";
const page_cart = "cart";

function App() {
  const [cart, setCart] = useState([]);
  const [page, setPage] = useState([page_items]);

  const addToCart = (item) => {
    setCart([...cart, item]);
  };

  const navigateTo = (nextPage) => {
    setPage(nextPage);
  };
  const removeFromCart = (item) => {
    const updatedCart = cart.filter((element) => element.name !== item.name);

    setCart(updatedCart);
  };

  return (
    <div className="App">
      <header>
        <button onClick={() => navigateTo(page_cart)}>
          Go to Cart ({cart.length})
        </button>
        <button onClick={() => navigateTo(page_items)}>Show Items</button>
      </header>

      {page === page_items && <Items addToCart={addToCart} />}
      {page === page_cart && (
        <Cart cart={cart} removeFromCart={removeFromCart} />
      )}
    </div>
  );
}
export default App;
