import React from "react";

export default function Cart({ cart, removeFromCart }) {
  return (
    <>
      <h1>Cart</h1>
      <div className="items">
        {cart.map((item, idx) => (
          <div className="item" key={idx}>
            <h3> {item.name} </h3>
            <h4> {item.cost} </h4>
            <img src={item.image} alt={item.name} />
            <button onClick={() => removeFromCart(item)}>
              Remove from Cart
            </button>
          </div>
        ))}
      </div>
    </>
  );
}
